	export default {
		name:"km-input",
		props:{
			"title":{
				default:''
			},
			"unit":{
				default:' '
			},
			"inputType":{
				default:'text'
			},
			"iptValue":{
				default:''
			},
			"error":{
				default:0
			},
			"isDisabled":{
				default:false
			}
		},
		watch:{
			'iptValue': function(newVal, oldVal){
				this.value = newVal;
			},
			value : function(newVal, oldVal){
                this.$emit("update:iptValue", newVal);
            }
		},
		data() {
			return {
				value: this.iptValue,
				dir:0	//0:字体居左;1:字体居右
			};
		},
		methods:{
			textDir(dir) {
				this.dir = dir;
			},
			inputBlur() {
				this.$emit("inputBlur")
			}
		}
	}
